.services {
    padding: 50px;
    background-color: #fff;
    text-align: center;
  }
  
  .services h2 {
    margin-bottom: 40px;
    font-size: 2.5rem;
  }
  
  .service-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .service-item {
    width: 30%;
    padding: 20px;
    border: 1px solid #ccc;
    margin: 10px;
    border-radius: 10px;
  }
  
  .service-item h3 {
    margin-bottom: 10px;
    font-size: 1.8rem;
  }
  
  .service-item p {
    font-size: 1.2rem;
  }
  