.contact {
    padding: 50px;
    background-color: #f5f5f5;
    text-align: center;
  }
  
  .contact form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .contact form label {
    margin-bottom: 15px;
  }
  
  .contact form input,
  .contact form textarea {
    width: 100%;
    max-width: 400px;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  .contact form button {
    padding: 10px 20px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .contact form button:hover {
    background-color: #575757;
  }
  